.navbar {
  background-color: #663399; /* Purple */
  color: white;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.8rem;
}

.actions button {
  background-color: #ff6347; /* Tomato */
  color: white;
  border: none;
  padding: 8px 16px;
  margin-left: 10px;
  cursor: pointer;
}

.actions button:hover {
  background-color: #ff4500; /* Orange Red */
}

.actions div {
  display: flex;
  align-items: center;
}

.actions div button {
  margin-left: 10px;
}
